import tranHistory from './transactions.json'


// let colN = Color()
let CASTOM_ID = 0;

function gap() {
    let in_turn = false
    return () => in_turn ? in_turn = false :  in_turn = true     
}

let InTurn = gap()


function Content() {


    return (
        
            
        tranHistory.map(obj => {
            const id = obj.id ? obj.id.toString() : 'id-z0'+(++CASTOM_ID);                   
            const type = obj.type ? obj.type : 'missing type'
            const amount = obj.amount ? obj.amount : 'missing amount'
            const currency = obj.currency ? obj.currency : 'missing currency'
            const DopClass = InTurn() ? 'in_turn' : 'clasic'
            
                    

            return(
                <tr key={id} id={id} className={ DopClass }>
                <td>{ type }</td>
                <td>{ amount }</td>
                <td>{ currency }</td>
            </tr>
            )
                })
            

            
       
    )
}

export default Content