import Content from './Content.jsx'
import './transactions.css'

function Transactions() {
    return(
        <table className="transaction-history">
            <thead>
                <tr>
                <th>Type</th>
                <th>Amount</th>
                <th>Currency</th>
                </tr>
            </thead>

            <tbody>
                <Content />
            </tbody>
        </table>
    )
}

export default Transactions